function updateCartCount() {
  if (typeof InpostIziJavsscriptWoocommerce  === "object") {
    const timestamp = new Date().getTime();
    jQuery.ajax({
      url: `${InpostIziJavsscriptWoocommerce.ajaxurl}?t=${timestamp}`,
      type: "POST",
      data: { action: "update_cart_count" },
      success: function (response) {
        window.iziCartCount = response;
        jQuery(document.body).trigger("izi_cart_count_updated");
      },
    });
  }
}

if (typeof InpostIziJavsscriptWoocommerce  === "object") {
  jQuery(document.body).on(
    "added_to_cart removed_from_cart wc_fragment_refresh updated_cart_totals",
    function () {
      updateCartCount();
    }
  );
}
